import yangrMsg from "@/components/yangr-msg/yangr-msg.vue"
export default {
	components: {yangrMsg},	
	data() {
		return {
			title: '匹配成功',
			info1: '',
			info2: '',
			Show1: false,
			Show2: false,
		}
	},
	methods: {
		closeYangrMsg(){
			this.Show1 = false;
			this.Show2 = false;
		},
		rank:function(event){
			uni.navigateTo({
				url: '../rank/rank'
			})
		},

		
		matchWild: function (event) {
			var me = this;
			const db = wx.cloud.database();
			const waitCollection = db.collection('wait');
			var total = 0;
			
			waitCollection.count({
				success: function(res) {
					console.log(res.total);
					total = res.total;
					var watcher;
					if(total == 0){
						waitCollection.add({data: {}})
						watcher = waitCollection.watch({
						  onChange: function(snapshot) {
						    console.log('snapshot1', snapshot)
							if(snapshot.docs.length == 2){
								console.log('1号的对手', snapshot.docs[1]._openid)
								var his_nickName = '';
								db.collection('login_users').where({
									_openid: snapshot.docs[1]._openid
								}).get().then(get_res => {
								  console.log('->', get_res.data[0].nickName)
								  his_nickName = get_res.data[0].nickName;
									me.info1 = '对手: '+his_nickName;
									me.Show1 = true;

										wx.cloud.callFunction({
											name: 'deleteData',
											env: 'liaohch3-j5hup',
											data: {}
										})

									
								})
							}
						  },
						  onError: function(err) {
						    console.error('the watch closed because of error', err)
						  }
						})
					}else if(total == 1){
						
						waitCollection.get() 
						.then(res => {
							var his_nickName = '';
							db.collection('login_users').where({
								_openid: res.data[0]._openid
							}).get().then(get_res => {
							  console.log('->', get_res.data[0].nickName)
							  his_nickName = get_res.data[0].nickName;
								me.info2 = '对手: '+his_nickName;
								me.Show2 = true;
							})
						})
						
						waitCollection.add({data: {}})
						watcher.close()

							wx.cloud.callFunction({
								name: 'deleteData',
								env: 'liaohch3-j5hup',
								data: {}
							})

					}
				},
				fail: console.error
			})
			
		},

		

		matchBaller: function () {
			uni.navigateTo({
				url: '../me/me'
			});
		}

	}
}
